@charset "UTF-8";

.tablecontent__row {
  display: flex;
  padding: 30px 0;
  border-bottom: 1px solid #D9D9D9;
}

.tablecontent__row:first-child {
  border-bottom: 1px solid transparent;
}

.tablecontent__image {
  max-width: 50vw;
}

.tablecontent__info {
  display: flex;
  flex-flow: column wrap;
  justify-content: space-evenly;
}

.tablecontent__main {
  display: inline-flex;
}

.tablecontent__deatails {
  display: flex;
  flex-flow: row wrap;
  align-items: flex-end;
}

.tablecontent__link {
  margin-right: 1rem;
}

.tablecontent__deatails > div {
  margin-right: 0.25rem;
}

.tablecontent__model {
  display: flex;
  border-bottom: 1px solid #D9D9D9;
  flex-flow: row nowrap;
  align-items: center;
}

.tablecontent__art {
  padding: 2px 10px;
  background: #969696;
  font-size: 13px;
  line-height: 17px;
  font-weight: 700;
  color: #fff;
  display: none;
  overflow: hidden;
}

.tablecontent__volume,
.tablecontent__sections,
.tablecontent__dia,
.tablecontent__chassis,
.tablecontent__pricevalue {
  font-size: 18px;
}

.tablecontent__volume {
  font-weight: 700;
}

.tablecontent__volume span {
  font-weight: normal;
}

.tablecontent__dia {
  font-weight: 700;
}

.tablecontent__sections {
  font-weight: 700;
}

.tablecontent__pricevalue {
  font-family: squares, arial;
  font-weight: 700;
  font-size: 22px;
  line-height: 30px;
}

.tablecontent__defenitions > * {
  font-size: 18px !important;
  border-top: 1px solid #D9D9D9;
  border-bottom: 1px solid #D9D9D9;
  padding: 20px 0;
  font-weight: 400 !important;
  font-family: "segoeui", "arial" !important;
  display: none;
}

/********************* Таблица в карточке товара *****************/

.tabs__truckcard {
  max-width: 1200px;
  margin: auto;
}

.tabs__truckcard .tablecontent__deatails,
.tabs__truckcard .tablecontent__main {
  max-width: initial;
}

.tabs__truckcard .tablecontent__row {
  display: block;
}

.tabs__truckcard .tablecontent__row:first-child {
  grid-template-columns: 15rem 1fr 1fr 1fr 2fr 2fr;
}

.tabs__truckcard .tablecontent__image {
  display: none;
}

.tabs__truckcard .tablecontent__info {
  grid-template-columns: 15rem 5fr 2fr;
}

.tabs__truckcard .tablecontent__art {
  display: block;
}

.tabs__truckcard .tablecontent__pricevalue {
  font-size: 18px;
}

@media only screen and (min-width: 768px) {
  .tablecontent__image {
    max-width: 30%;
  }

  .tablecontent__info {
    flex: 1;
    flex-flow: row wrap;
    justify-content: flex-start;
  }

  .tablecontent__main,
  .tablecontent__deatails {
    max-width: 40vw;
    flex: 1 60%;
  }

  .tablecontent__price {
    flex: 1 40%;
    max-width: 30vw;
    text-align: right;
  }

  .tablecontent__pricevalue {
    text-align: right;
  }
}

@media only screen and (min-width: 1024px) {
  .tablecontent__row {
    display: grid;
    grid-template-columns: 14rem auto;
  }

  .tablecontent__row:first-child {
    display: grid;
    grid-template-columns: 21rem 1fr 1fr 1fr 2fr 2fr;
    grid-template-rows: auto;
    gap: 0px 0px;
  }

  .tablecontent__image {
    max-width: initial;
  }

  .tablecontent__info {
    display: grid;
    grid-template-columns: 7rem 5fr 2fr;
    align-items: center;
  }

  .tablecontent__deatails {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 2fr;
  }

  .tablecontent__defenitions > * {
    display: block;
  }
}

@media only screen and (max-width: 767px) {
  .tablecontent__pricevalue {
    margin-top: 16px;
  }
}